export default {
	data() {
		return {
			items:[
				{
					value:"5",
					name:"5",
					checked:true
				},
				{
					value:"10",
					name:"10",
					checked:false
				},
				{
					value:"20",
					name:"10",
					checked:false
				},{
					value:"30",
					name:"30",
					checked:false
				},
				{
					value:"40",
					name:"40",
					checked:false
				},
				{
					value:"50",
					name:"50",
					checked:false
				}
			],
		};
	},
	methods:{
		changed(e){
			let data = e.detail.value;
			let items = JSON.parse(JSON.stringify(this.items))
			for(let item of items) {
				item['checked'] = item.value === data
			}
			this.items = items;
		},
		recharge(){
			uni.navigateTo({
			  url: "userInfo"
			});
		},
		goHistory(){
			uni.navigateTo({
			  url: "history"
			});
		}
	}
}
